::root{
	--slider-transform:0;
	--transition: trasform 1s;
}
.slider{
	display: flex;
	transform: translateX(var(--slider-transform));
	transition: var(--transition);
}

 .slider__container{
	width: 300px;
	margin-left: auto;
	margin-right: auto;
	border: 4px solid #fff;
	
	}
.slider__element{
		min-width:100%;
		height: 300px;
		font-size: 3rem;
		text-align: center;
		line-height: 300px;
	}
.slider__element--c{
	background-color: lightcoral;
}
.slider__element--v{
	background-color: lightgreen;
}
.slider__element--b{
	background-color: lightblue;
}
.slider__element--n{
	background-color: lightsalmon;
}

